import React, { useState, useEffect, useRef } from "react";
import { Minimize2, Maximize2, X } from "lucide-react";
import { appRegistry } from "../../utils/appRegistry";

const Window = ({
  window: win,
  activeWindow,
  setActiveWindow,
  onMinimize,
  onMaximize,
  onClose,
  onUpdatePosition,
  fileSystem,
  saveFile,
  createDirectory,
  deleteFile,
  reloadFileSystem,
  userId,
  currentPath,
  onOpenFile,
  createWindow,
  currentUser,
  updateTheme,
  updateDesktopLayout,
}) => {
  const [position, setPosition] = useState({ x: win.x, y: win.y });
  const [isDragging, setIsDragging] = useState(false);
  const dragStart = useRef({ x: 0, y: 0 });

  // -------------------------------------------------------
  // Resolve app + component safely
  // -------------------------------------------------------
  const app = appRegistry[win.app];

  if (!app) {
    console.error(
      ` Window Error: Unknown app type "${win.app}". Add it to appRegistry.`
    );
  }

  const Component = app?.Component;
  const AppIcon = app?.icon;

  // -------------------------------------------------------
  // Dragging logic
  // -------------------------------------------------------
  const handleMouseDown = (e) => {
    if (e.target.closest(".window-content") || e.target.closest("button"))
      return;

    setIsDragging(true);
    dragStart.current = {
      x: e.clientX - position.x,
      y: e.clientY - position.y,
    };

    setActiveWindow(win.id);
  };

  useEffect(() => {
    const handleMouseMove = (e) => {
      if (isDragging) {
        const newPos = {
          x: e.clientX - dragStart.current.x,
          y: e.clientY - dragStart.current.y,
        };
        setPosition(newPos);
        onUpdatePosition(win.id, newPos.x, newPos.y);
      }
    };

    const handleMouseUp = () => setIsDragging(false);

    if (isDragging) {
      document.addEventListener("mousemove", handleMouseMove);
      document.addEventListener("mouseup", handleMouseUp);
    }

    return () => {
      document.removeEventListener("mousemove", handleMouseMove);
      document.removeEventListener("mouseup", handleMouseUp);
    };
  }, [isDragging, win.id, onUpdatePosition]);

  // -------------------------------------------------------
  // Minimized windows do not render
  // -------------------------------------------------------
  if (win.minimized) return null;

  // -------------------------------------------------------
  // Window size + position
  // -------------------------------------------------------
  const style = win.maximized
    ? {
        left: 0,
        top: 0,
        width: "100%",
        height: "calc(100% - 48px)",
        position: "absolute",
      }
    : {
        left: position.x,
        top: position.y,
        width: win.width,
        height: win.height,
        position: "absolute",
      };

  // -------------------------------------------------------
  // Props passed to the app component
  // -------------------------------------------------------
  const componentProps = {
    windowId: win.id,
    fileSystem,
    saveFile,
    createDirectory,
    deleteFile,
    reloadFileSystem,
    userId,
    currentPath,
    onOpenFile,
    createWindow,
    currentUser,
    updateTheme,
    updateDesktopLayout,
    ...win,
  };

  // -------------------------------------------------------
  // Render
  // -------------------------------------------------------
  return (
    <div
      style={{
        ...style,
        background: "white",
        borderRadius: "8px",
        boxShadow: "0 20px 60px rgba(0,0,0,0.2)",
        display: "flex",
        flexDirection: "column",
        overflow: "hidden",
        minWidth: "1280px",
        minHeight: "1080px",
        zIndex: activeWindow === win.id ? 10 : 0,
      }}
      onMouseDown={() => setActiveWindow(win.id)}
    >
      {/* Title Bar */}
      <div
        onMouseDown={handleMouseDown}
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          padding: "8px 12px",
          cursor: "move",
          userSelect: "none",
          background: activeWindow === win.id ? "#879b93d8" : "#000000",
          color: activeWindow === win.id ? "white" : "#374151",
          opacity: 0.4,
        }}
      >
        <div style={{ display: "flex", alignItems: "center", gap: "8px" }}>
          {AppIcon && <AppIcon size={16} />}
          <span style={{ fontWeight: 600, fontSize: "18px" }}>
            {win.title || app?.title || "Unknown App"}
          </span>
        </div>

        <div style={{ display: "flex", alignItems: "center", gap: "4px" }}>
          <button
            onClick={() => onMinimize(win.id)}
            style={buttonStyle}
          >
            <Minimize2 size={16} />
          </button>
          <button
            onClick={() => onMaximize(win.id)}
            style={buttonStyle}
          >
            <Maximize2 size={16} />
          </button>
          <button
            onClick={() => onClose(win.id)}
            style={buttonStyle}
          >
            <X size={16} />
          </button>
        </div>
      </div>

      {/* Window Content */}
      <div className="window-content" style={{ flex: 1, overflow: "hidden" }}>
        {Component ? (
          <Component {...componentProps} />
        ) : (
          <div
            style={{
              padding: "20px",
              color: "#b91c1c",
              fontFamily: "monospace",
              fontSize: "14px",
            }}
          >
            <strong>App Load Error</strong>
            <br />
            No Component found for app type:
            <br />
            <code>{win.app}</code>
            <br />
            <br />
            Check your <code>appRegistry</code>.
          </div>
        )}
      </div>
    </div>
  );
};

// -------------------------------------------------------
// Styles
// -------------------------------------------------------

const buttonStyle = {
  padding: "4px",
  background: "#92bc98",
  border: "none",
  borderRadius: "12px",
  cursor: "pointer",
};

export default Window;