export class FileSearchController {
  constructor(fileSystem) {
    // fileSystem will be injected later by AirOS
    this.fs = fileSystem || null;
  }

  // -----------------------------
  // Safe getter for all files
  // -----------------------------
  getAllFiles() {
    if (!this.fs) return [];

    // Your file system is a flat object: { path: fileObj }
    return Object.entries(this.fs).map(([path, file]) => ({
      id: file.id || path,
      name: file.name || path.split('/').pop(),
      path,
      type: file.type || 'file',
      thumbnail: file.thumbnail || null,
      content: file.content
    }));
  }

  // -----------------------------
  // Search logic
  // -----------------------------
  search(query) {
    if (!query || !this.fs) return [];

    const lower = query.toLowerCase();
    const files = this.getAllFiles();

    return files.filter((file) => {
      return (
        file.name.toLowerCase().includes(lower) ||
        file.path.toLowerCase().includes(lower) ||
        (file.type && file.type.toLowerCase().includes(lower))
      );
    });
  }
}