import React, { useState, useEffect } from 'react';

// core utilities
import { historyService } from '../../../core/browser/historyService';

// browser UI components
import { BrowserToolbar } from './components/BrowserToolbar';
import { TabBar } from './components/TabBar';
import { BrowserContent } from './components/BrowserContent';

const createTab = (url) => ({
  id: crypto.randomUUID?.() ?? `${Date.now()}-${Math.random()}`,
  url,
});

const DEFAULT_URL = 'https://www.wikipedia.com';

// Detect URL or search query
const resolveUrl = (input) => {
  if (!input) return DEFAULT_URL;

  const trimmed = input.trim();

  const looksLikeUrl =
    trimmed.startsWith('http://') ||
    trimmed.startsWith('https://') ||
    /^[\w-]+\.[a-z]{2,}$/i.test(trimmed);

  if (looksLikeUrl) {
    return trimmed.startsWith('http') ? trimmed : `https://${trimmed}`;
  }

  return `https://www.google.com/search?q=${encodeURIComponent(trimmed)}`;
};

export const BrowserApp = ({ startURL }) => {
  const initialUrl = startURL ? resolveUrl(startURL) : DEFAULT_URL;

  // Initialize tabs safely
  const [tabs, setTabs] = useState(() => {
    const first = createTab(initialUrl);
    return [first];
  });

  // Initialize active tab safely
  const [activeTabId, setActiveTabId] = useState(() => {
    return tabs[0]?.id || null;
  });

  // Ensure activeTabId is always valid
  useEffect(() => {
    if (!activeTabId && tabs.length > 0) {
      setActiveTabId(tabs[0].id);
    }
  }, [tabs, activeTabId]);

  const activeTab = tabs.find((t) => t.id === activeTabId) ?? tabs[0];

  const isElectron = navigator.userAgent.includes('Electron');
  const PROXY = 'http://localhost:5050/proxy?url=';
  const proxied = (url) => `${PROXY}${encodeURIComponent(url)}`;

  const effectiveUrl = activeTab
    ? (isElectron ? proxied(activeTab.url) : activeTab.url)
    : DEFAULT_URL;

  console.log('Effective URL:', effectiveUrl);

  // Navigation
  const navigate = (tabId, rawUrl) => {
    const finalUrl = resolveUrl(rawUrl);

    setTabs((prev) =>
      prev.map((tab) =>
        tab.id === tabId ? { ...tab, url: finalUrl } : tab
      )
    );

    historyService.add(finalUrl);
  };

  const handleNewTab = () => {
    const tab = createTab(DEFAULT_URL);
    setTabs((prev) => [...prev, tab]);
    setActiveTabId(tab.id);
  };

  const handleCloseTab = (tabId) => {
    setTabs((prev) => {
      const filtered = prev.filter((t) => t.id !== tabId);

      if (filtered.length === 0) {
        const fresh = createTab(DEFAULT_URL);
        setActiveTabId(fresh.id);
        return [fresh];
      }

      if (tabId === activeTabId) {
        setActiveTabId(filtered[filtered.length - 1].id);
      }

      return filtered;
    });
  };

  if (!activeTab) return null;

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
        background: 'white',
      }}
    >
      <TabBar
        tabs={tabs}
        activeTabId={activeTabId}
        onSelect={setActiveTabId}
        onNewTab={handleNewTab}
        onCloseTab={handleCloseTab}
      />

      <BrowserToolbar
        currentUrl={activeTab.url}
        onNavigate={(url) => navigate(activeTab.id, url)}
      />

      <BrowserContent url={effectiveUrl} />
    </div>
  );
};