import { FileSearchController } from './FileSearchController';

export class SearchPaletteController {
  constructor({ browserController, fileSystem }) {
    this.browser = browserController || null;
    this.fileSearch = new FileSearchController(fileSystem);

    this.visible = false;
    this.listeners = [];
    this.lastResult = null;
  }

  show() {
    this.visible = true;
    this.notify();
  }

  hide() {
    this.visible = false;
    this.lastResult = null;
    this.notify();
  }

  subscribe(fn) {
    this.listeners.push(fn);
    return () => {
      this.listeners = this.listeners.filter(l => l !== fn);
    };
  }

  notify() {
    this.listeners.forEach(fn => fn());
  }

  isURL(input) {
    return (
      input.startsWith('http://') ||
      input.startsWith('https://') ||
      /^[\w-]+\.[a-z]{2,}$/i.test(input)
    );
  }

  handleQuery(query) {
    const trimmed = query.trim();
    if (!trimmed) return { type: 'empty' };

    if (this.isURL(trimmed)) {
      const finalURL = trimmed.startsWith('http')
        ? trimmed
        : `https://${trimmed}`;
      return { type: 'url', url: finalURL };
    }

    const fileResults = this.fileSearch.search(trimmed);
    if (fileResults.length > 0) {
      return { type: 'files', results: fileResults };
    }

    return { type: 'web', query: trimmed };
  }

  submit(query, launchApp) {
    const result = this.handleQuery(query);
    this.lastResult = result;

    switch (result.type) {
      case 'url':
        launchApp('Browser', { startURL: result.url });
        this.hide();
        break;

      case 'files':
        this.show();
        break;

      case 'web':
        const encoded = encodeURIComponent(result.query);
        launchApp('Browser', {
          startURL: `https://www.google.com/search?q=${encoded}`
        });
        this.hide();
        break;
    }

    return result;
  }
}