import React, { useCallback, useRef } from "react";
import { serializeToAirdoc } from "./airdocFormat";

const wrapSelection = (text, selectionStart, selectionEnd, wrapper) => {
  const before = text.slice(0, selectionStart);
  const selected = text.slice(selectionStart, selectionEnd);
  const after = text.slice(selectionEnd);

  const wrapped = `${wrapper}${selected}${wrapper}`;
  const newText = before + wrapped + after;

  const newStart = selectionStart + wrapper.length;
  const newEnd = newStart + selected.length;

  return { newText, newStart, newEnd };
};

export const WordProcessorWindow = ({
  value,
  onChange,
  fileName = "Untitled",
  onSavePlain,   // (text: string) => void
  onSaveAirdoc,  // (airdocJson: string) => void
}) => {
  const textareaRef = useRef(null);

  const handleKeyDown = useCallback(
    (e) => {
      const textarea = textareaRef.current;
      if (!textarea) return;

      const { selectionStart, selectionEnd } = textarea;

      // TAB  insert indentation
      if (e.key === "Tab") {
        e.preventDefault();

        const before = value.slice(0, selectionStart);
        const after = value.slice(selectionEnd);
        const newValue = `${before}\t${after}`;

        onChange(newValue);

        requestAnimationFrame(() => {
          textarea.selectionStart = textarea.selectionEnd = selectionStart + 1;
        });
        return;
      }

      // Ctrl+B / Ctrl+I / Ctrl+U  markdown-style formatting
      if (e.ctrlKey && !e.shiftKey && !e.altKey) {
        let wrapper = null;

        if (e.key.toLowerCase() === "b") {
          e.preventDefault();
          wrapper = "**"; // **bold**
        } else if (e.key.toLowerCase() === "i") {
          e.preventDefault();
          wrapper = "_"; // _italic_
        } else if (e.key.toLowerCase() === "u") {
          e.preventDefault();
          wrapper = "__"; // __underline__
        }

        if (wrapper) {
          const { newText, newStart, newEnd } = wrapSelection(
            value,
            selectionStart,
            selectionEnd,
            wrapper
          );
          onChange(newText);

          requestAnimationFrame(() => {
            textarea.selectionStart = newStart;
            textarea.selectionEnd = newEnd;
          });
        }
      }
    },
    [value, onChange]
  );

  const applyWrapperFromToolbar = (wrapper) => {
    const textarea = textareaRef.current;
    if (!textarea) return;

    const { selectionStart, selectionEnd } = textarea;
    const { newText, newStart, newEnd } = wrapSelection(
      value,
      selectionStart,
      selectionEnd,
      wrapper
    );
    onChange(newText);

    requestAnimationFrame(() => {
      textarea.selectionStart = newStart;
      textarea.selectionEnd = newEnd;
    });
  };

  const handleExportAirdoc = () => {
    const airdoc = serializeToAirdoc({
      content: value,
      meta: { title: fileName },
    });
    onSaveAirdoc?.(airdoc);
  };

  const handleExportPdf = () => {
    const html = generatePrintableHtml(value, fileName);
    const w = window.open("", "_blank");
    if (!w) return;

    w.document.write(html);
    w.document.close();
    w.focus();
    w.print(); // user can choose "Save as PDF"
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        height: "100%",
        background: "#0f172a",
        color: "#e5e7eb",
        borderRadius: 8,
        overflow: "hidden",
      }}
    >
      {/* Title / Header */}
      <div
        style={{
          padding: "6px 10px",
          borderBottom: "1px solid #1f2937",
          fontSize: 12,
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          background: "#020617",
        }}
      >
        <span style={{ opacity: 0.8 }}>{fileName}</span>
        <span style={{ fontSize: 11, opacity: 0.6 }}>Kite Docs</span>
      </div>

      {/* Toolbar */}
      <div
        style={{
          padding: "4px 8px",
          borderBottom: "1px solid #1f2937",
          display: "flex",
          gap: 6,
          background: "#020617",
          alignItems: "center",
        }}
      >
        <button
          type="button"
          onClick={() => applyWrapperFromToolbar("**")}
          style={buttonStyle}
        >
          B
        </button>
        <button
          type="button"
          onClick={() => applyWrapperFromToolbar("_")}
          style={buttonStyle}
        >
          I
        </button>
        <button
          type="button"
          onClick={() => applyWrapperFromToolbar("__")}
          style={buttonStyle}
        >
          U
        </button>

        <div style={{ width: 1, height: 18, background: "#73cf84", margin: "0 6px" }} />

        <button
          type="button"
          onClick={() => onSavePlain?.(value)}
          style={buttonStyle}
        >
          Save
        </button>
        <button
          type="button"
          onClick={handleExportAirdoc}
          style={buttonStyle}
        >
          Save as .airdoc
        </button>
        <button
          type="button"
          onClick={handleExportPdf}
          style={buttonStyle}
        >
          Export PDF
        </button>

        <div style={{ flex: 1 }} />
        <span style={{ fontSize: 11, opacity: 0.5 }}>
          Tab = indent  Ctrl+B/I/U = format  Spellcheck on
        </span>
      </div>

      {/* Editor */}
      <textarea
        ref={textareaRef}
        value={value}
        onChange={(e) => onChange(e.target.value)}
        onKeyDown={handleKeyDown}
        spellCheck={true}
        lang="en"
        style={{
          flex: 1,
          width: "100%",
          resize: "none",
          border: "solid 2px",
          outline: "none",
          padding: 12,
          fontFamily: "system-ui, -apple-system, BlinkMacSystemFont, sans-serif",
          fontSize: 14,
          lineHeight: 1.5,
          background: "#d3e8e0",
          color: "#000000",
          tabSize: 2,
          whiteSpace: "pre-wrap",
        }}
      />
    </div>
  );
};

const buttonStyle = {
  padding: "2px 8px",
  fontSize: 11,
  borderRadius: 4,
  border: "1px solid #1f2937",
  background: "#020617",
  color: "#e5e7eb",
  cursor: "pointer",
};

function generatePrintableHtml(content, title) {
  const escaped = content
    .replace(/&/g, "&amp;")
    .replace(/</g, "&lt;")
    .replace(/>/g, "&gt;")
    .replace(/\n/g, "<br/>");

  return `
<!doctype html>
<html>
<head>
  <meta charset="utf-8" />
  <title>${title}</title>
  <style>
    body {
      font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
      padding: 40px;
      line-height: 1.5;
      color: #111827;
    }
  </style>
</head>
<body>
  ${escaped}
</body>
</html>
`;
}