// airdocFormat.js (or inline in the same file if you want fewer files)

export const AIRDOC_MIME = "application/x-airdoc+json";

export function serializeToAirdoc({ content, meta = {} }) {
  return JSON.stringify({
    version: 1,
    type: "airdoc",
    meta: {
      title: meta.title || "Untitled",
      createdAt: meta.createdAt || new Date().toISOString(),
      updatedAt: new Date().toISOString(),
      ...meta,
    },
    body: {
      kind: "plain-text-v1",
      content,
    },
  });
}

export function deserializeFromAirdoc(raw) {
  try {
    const parsed = JSON.parse(raw);
    if (parsed?.type === "airdoc" && parsed?.body?.content != null) {
      return {
        content: String(parsed.body.content),
        meta: parsed.meta || {},
      };
    }
  } catch (e) {
    // not airdoc, fall through
  }
  return { content: raw, meta: {} };
}