import { supabase } from '../../utils/supabase';

/**
 * Fetch messages for a given channel.
 * channelType: 'global' | 'dm' | 'team' | etc.
 * channelId: string or UUID
 */
export async function fetchChannelMessages(channelType, channelId, limit = 200) {
  const { data, error } = await supabase
    .from('messages')
    .select('*')
    .eq('channel_type', channelType)
    .eq('channel_id', channelId)
    .order('created_at', { ascending: true })
    .limit(limit);

  if (error) throw error;
  return data || [];
}

/**
 * Send a message to a channel.
 */
export async function sendMessage({ senderId, channelType, channelId, content }) {
  const { data, error } = await supabase
    .from('messages')
    .insert({
      sender_id: senderId,
      channel_type: channelType,
      channel_id: channelId,
      content
    })
    .select()
    .single();

  if (error) throw error;
  return data;
}

/**
 * Subscribe to new messages for a channel.
 */
export function subscribeToChannelMessages(channelType, channelId, onMessage) {
  const channel = supabase
    .channel(`messages-${channelType}-${channelId}`)
    .on(
      'postgres_changes',
      {
        event: 'INSERT',
        schema: 'public',
        table: 'messages',
        filter: `channel_type=eq.${channelType},channel_id=eq.${channelId}`
      },
      payload => {
        onMessage(payload.new);
      }
    )
    .subscribe();

  return () => {
    supabase.removeChannel(channel);
  };
}

/**
 * Optional helper: global chat channel descriptor.
 * You can also store this in a `channels` table if you prefer.
 */
export function getGlobalChannel() {
  return { type: 'global', id: 'global' };
}