import React, { useEffect, useMemo, useState } from "react";

const TILT = 0.55;

// ---------------------------------------------------------
// Config
// ---------------------------------------------------------

const DEBUG_OVERLAYS = false; // flip to true to see debug info
const BURST_BASE_RADIUS = 120; // tuned for 45 apps
const BURST_DEPTH_SCALE = 20;

// ---------------------------------------------------------
// Utils
// ---------------------------------------------------------

function lerp(a, b, t) {
  return a + (b - a) * t;
}

function computeRadialBurst(apps, cx, cy, radius) {
  if (!apps || apps.length === 0) return [];
  const count = apps.length;
  const step = (Math.PI * 2) / count;

  return apps.map((_, i) => {
    const angle = i * step - Math.PI / 2;
    return {
      x: cx + Math.cos(angle) * radius,
      y: cy + Math.sin(angle) * radius * TILT,
    };
  });
}

// ---------------------------------------------------------
// HUD Elements
// ---------------------------------------------------------

function HoloRing({ cx, cy, radius, depth }) {
  return (
    <svg
      style={{
        position: "absolute",
        inset: 0,
        width: "100%",
        height: "100%",
        pointerEvents: "none",
      }}
    >
      <g transform={`translate(${cx} ${cy}) scale(1 ${TILT})`}>
        <circle
          cx={0}
          cy={0}
          r={radius}
          fill="none"
          stroke={`rgba(248, 250, 252, ${0.12 + depth * 0.18})`}
          strokeWidth={1}
          strokeDasharray="4 6"
          style={{
            filter: `drop-shadow(0 0 ${24 * depth}px rgba(249, 115, 22, ${
              0.35 * depth
            }))`,
          }}
        />
      </g>
    </svg>
  );
}

function HoloArc({ cx, cy, radius, depth }) {
  const start = -Math.PI * 0.35;
  const end = Math.PI * 0.35;

  const x1 = Math.cos(start) * radius;
  const y1 = Math.sin(start) * radius * TILT;
  const x2 = Math.cos(end) * radius;
  const y2 = Math.sin(end) * radius * TILT;

  const d = `M ${x1} ${y1} A ${radius} ${radius * TILT} 0 0 1 ${x2} ${y2}`;

  return (
    <svg
      style={{
        position: "absolute",
        inset: 0,
        width: "100%",
        height: "100%",
        pointerEvents: "none",
      }}
    >
      <g transform={`translate(${cx} ${cy})`}>
        <path
          d={d}
          fill="none"
          stroke={`rgba(248, 250, 252, ${0.18 * depth})`}
          strokeWidth={1.2}
          strokeLinecap="round"
        />
      </g>
    </svg>
  );
}

function AppNode({ x, y, appId, depth, onLaunchApp }) {
  const sizeSeed = appId ? (appId.charCodeAt(0) % 7) / 7 : 0.5;
  const size = 28 + depth * 14 + sizeSeed * 10;
  const blur = (1 - depth) * 1.2;

  return (
    <div
      style={{
        position: "absolute",
        left: x - size / 2,
        top: y - size / 2,
        width: size,
        height: size,
        borderRadius: "999px",
        background:
          "radial-gradient(circle at 30% 20%, rgba(248, 250, 252, 0.9), rgba(249, 115, 22, 0.1))",
        border: "1px solid rgba(248, 250, 252, 0.35)",
        boxShadow: `
          0 0 10px rgba(15, 23, 42, 0.9),
          0 0 ${18 * depth}px rgba(249, 115, 22, ${0.7 * depth}),
          0 0 ${40 * depth}px rgba(249, 115, 22, ${0.35 * depth})
        `,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        cursor: "pointer",
        filter: `blur(${blur}px)`,
        transition:
          "transform 0.18s ease, box-shadow 0.18s ease, filter 0.18s ease, border-color 0.18s ease",
      }}
      onClick={() => onLaunchApp(appId)}
      onMouseEnter={(e) => {
        e.currentTarget.style.transform = "scale(1.08)";
        e.currentTarget.style.filter = "blur(0px)";
        e.currentTarget.style.borderColor = "rgba(248, 250, 252, 0.9)";
      }}
      onMouseLeave={(e) => {
        e.currentTarget.style.transform = "scale(1)";
        e.currentTarget.style.filter = `blur(${blur}px)`;
        e.currentTarget.style.borderColor = "rgba(248, 250, 252, 0.35)";
      }}
    >
      <span
        style={{
          fontSize: 11 + depth * 2,
          color: "#0f172a",
          fontWeight: 600,
          textShadow: "0 0 4px rgba(248, 250, 252, 0.9)",
        }}
      >
        {appId?.[0]?.toUpperCase()}
      </span>
    </div>
  );
}

// ---------------------------------------------------------
// Main Cluster Node
// ---------------------------------------------------------

export default function ClusterNode({ group, time, onLaunchApp }) {
  const { constellation, cx, cy, depth, depthSeed, orbitRadius } = group;
  const {
    apps = [],
    name,
    color = "#f97316",
  } = constellation;

  const [sx, setSx] = useState(cx);
  const [sy, setSy] = useState(cy);
  const [expanded, setExpanded] = useState(false);
  const [selected, setSelected] = useState(false);

  // Smooth position
  useEffect(() => {
    setSx((prev) => lerp(prev, cx, 0.08));
    setSy((prev) => lerp(prev, cy, 0.08));
  }, [cx, cy]);

  // Motion
  const pulse = 1 + Math.sin(time * 0.0004 + depthSeed * 4.3) * 0.03;
  const ringRotation = time * 0.00003 * (depthSeed + 0.4);

  const burstRadius = BURST_BASE_RADIUS + depth * BURST_DEPTH_SCALE;

  const positions = useMemo(() => {
    if (!expanded || !apps || apps.length === 0) return [];
    return computeRadialBurst(apps, sx, sy, burstRadius);
  }, [expanded, apps, sx, sy, burstRadius]);

  const sortedPositions = useMemo(() => {
    return positions
      .map((p, i) => ({ ...p, i, z: p.y }))
      .sort((a, b) => a.z - b.z);
  }, [positions]);

  const collapsedSize = 56 + depth * 10;
  const collapsedGlow = 0.6 + depth * 0.4;

  const selectionBorderColor = selected
    ? "rgba(248, 250, 252, 0.95)"
    : "rgba(248, 250, 252, 0.45)";

  const selectionShadow = selected
    ? `
      0 0 24px rgba(248, 250, 252, 0.9),
      0 0 ${50 * collapsedGlow}px rgba(249, 115, 22, ${1.0 * collapsedGlow}),
      0 0 ${90 * collapsedGlow}px rgba(249, 115, 22, ${0.6 * collapsedGlow})
    `
    : `
      0 0 18px rgba(15, 23, 42, 0.9),
      0 0 ${40 * collapsedGlow}px rgba(249, 115, 22, ${0.9 * collapsedGlow}),
      0 0 ${80 * collapsedGlow}px rgba(249, 115, 22, ${0.45 * collapsedGlow})
    `;

  const handleClusterClick = () => {
    setSelected((prev) => !prev);
  };

  return (
    <>
      {/* Expanded state: orbit ring, arc, connections, app nodes */}
      {expanded && (
        <>
          <HoloRing
            cx={sx}
            cy={sy}
            radius={orbitRadius * 0.55 * depth}
            depth={depth}
          />
          <HoloArc
            cx={sx}
            cy={sy}
            radius={orbitRadius * 0.7 * depth}
            depth={depth}
          />

          {/* Connection lines from cluster center to each app */}
          <svg
            style={{
              position: "absolute",
              inset: 0,
              width: "100%",
              height: "100%",
              pointerEvents: "none",
            }}
          >
            {positions.map((p, i) => (
              <line
                key={i}
                x1={sx}
                y1={sy}
                x2={p.x}
                y2={p.y}
                stroke={color}
                strokeWidth={1}
                strokeOpacity={0.18 * depth}
              />
            ))}
          </svg>

          {/* App nodes */}
          {sortedPositions.map((p) => (
            <AppNode
              key={apps[p.i]}
              x={p.x}
              y={p.y}
              appId={apps[p.i]}
              depth={depth}
              onLaunchApp={onLaunchApp}
            />
          ))}
        </>
      )}

      {/* Collapsed holographic cluster node */}
      <div
        style={{
          position: "absolute",
          left: sx - collapsedSize / 2,
          top: sy - collapsedSize / 2,
          width: collapsedSize,
          height: collapsedSize,
          borderRadius: "999px",
          background:
            "radial-gradient(circle at 30% 20%, rgba(248, 250, 252, 0.95), rgba(249, 115, 22, 0.25))",
          boxShadow: selectionShadow,
          border: `1px solid ${selectionBorderColor}`,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          cursor: "pointer",
          transform: `scale(${pulse})`,
          transition:
            "transform 0.22s ease-out, box-shadow 0.22s ease-out, border-color 0.22s ease-out",
          overflow: "visible",
        }}
        onMouseEnter={() => setExpanded(true)}
        onMouseLeave={() => setExpanded(false)}
        onClick={handleClusterClick}
      >
        {/* Outer holographic ring */}
        <div
          style={{
            position: "absolute",
            inset: -10,
            borderRadius: "999px",
            border: "1px solid rgba(248, 250, 252, 0.25)",
            boxShadow: "0 0 18px rgba(248, 250, 252, 0.35)",
            opacity: 0.9,
            transform: `rotate(${ringRotation}rad)`,
            transition: "opacity 0.22s ease-out",
          }}
        />

        {/* Inner core */}
        <div
          style={{
            width: collapsedSize * 0.45,
            height: collapsedSize * 0.45,
            borderRadius: "999px",
            border: "2px solid rgba(15, 23, 42, 0.9)",
            boxShadow: "0 0 10px rgba(15, 23, 42, 0.9)",
            background:
              "radial-gradient(circle at 50% 30%, rgba(15,23,42,0.9), rgba(15,23,42,0.2))",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <span
            style={{
              fontSize: 11 + depth * 2,
              letterSpacing: 0.08,
              textTransform: "uppercase",
              color: "#0f172a",
              textShadow: "0 0 6px rgba(248, 250, 252, 0.9)",
              fontWeight: 600,
            }}
          >
            {name?.[0]?.toUpperCase() || ""}
          </span>
        </div>
      </div>

      {/* Label under collapsed node */}
      {name && (
        <div
          style={{
            position: "absolute",
            left: sx - 80,
            top: sy + collapsedSize * 0.7,
            width: 160,
            textAlign: "center",
            fontSize: 11,
            letterSpacing: 0.12,
            textTransform: "uppercase",
            color: `rgba(248, 250, 252, ${0.35 + depth * 0.35})`,
            pointerEvents: "none",
            filter: `blur(${(1 - depth) * 1.2}px)`,
          }}
        >
          {name}
        </div>
      )}

      {/* Debug overlays */}
      {DEBUG_OVERLAYS && (
        <div
          style={{
            position: "absolute",
            left: sx + collapsedSize * 0.6,
            top: sy - collapsedSize * 0.6,
            padding: "4px 8px",
            borderRadius: 6,
            background: "rgba(15,23,42,0.9)",
            color: "rgba(248,250,252,0.9)",
            fontSize: 10,
            fontFamily: "monospace",
            pointerEvents: "none",
            whiteSpace: "nowrap",
          }}
        >
          <div>{name || "Unnamed Cluster"}</div>
          <div>apps: {apps.length}</div>
          <div>depth: {depth.toFixed(2)}</div>
          <div>
            pos: {Math.round(sx)}, {Math.round(sy)}
          </div>
        </div>
      )}
    </>
  );
}