// src/hooks/useConstellations.js
import { useState } from "react";
import { defaultConstellations } from "../components/desktop/constellationSchema";

export function useConstellations(userId) {
  // In the future you can persist this per-user
  const [constellations, setConstellations] = useState(defaultConstellations);

  const updateConstellations = (next) => {
    setConstellations(next);
  };

  return { constellations, updateConstellations };
}