import React, { useState } from "react";
import LobbyScreen from "./LobbyScreen";
import VideoEditor from "./VideoEditor";

export default function ReelSmithApp() {
  const [screen, setScreen] = useState("lobby"); // "lobby" | "editor"
  const [currentProject, setCurrentProject] = useState(null);

  const handleCreateProject = () => {
    const newProject = {
      id: Date.now(),
      name: "Untitled Project",
      timeline: [],
      lastEdited: new Date().toISOString()
    };
    setCurrentProject(newProject);
    setScreen("editor");
  };

  const handleOpenProject = (fileOrProject) => {
    // If user uploads JSON
    if (fileOrProject.target) {
      const file = fileOrProject.target.files[0];
      const reader = new FileReader();
      reader.onload = () => {
        const project = JSON.parse(reader.result);
        setCurrentProject(project);
        setScreen("editor");
      };
      reader.readAsText(file);
      return;
    }

    // If user clicks a recent project
    setCurrentProject(fileOrProject);
    setScreen("editor");
  };

  return (
    <>
      {screen === "lobby" && (
        <LobbyScreen
          onCreateProject={handleCreateProject}
          onOpenProject={handleOpenProject}
          recentProjects={[]} // you can wire this later
          onEnterEditor={() => setScreen("editor")}
        />
      )}

      {screen === "editor" && (
        <VideoEditor
          project={currentProject}
          onExit={() => setScreen("lobby")}
        />
      )}
    </>
  );
}