import React, { useEffect, useMemo, useState } from "react";
import { useContextMenu } from "../../context/ContextMenuContext";
import { Calendar, Settings, LogOut } from "lucide-react";
import ClusterNode from "./ClusterNode";
import DesktopClockWidget from "../widgets/DesktopClockWidget";

// ---------------------------------------------------------
// CONFIG
// ---------------------------------------------------------

const TILT = 0.55;
const TIME_SLOW = 0.01;
const HUB_SCALE = 1.25;

// ---------------------------------------------------------
// VIEWPORT HOOK
// ---------------------------------------------------------

function useViewportSize() {
  const [size, setSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });

  useEffect(() => {
    const onResize = () =>
      setSize({ width: window.innerWidth, height: window.innerHeight });

    window.addEventListener("resize", onResize);
    return () => window.removeEventListener("resize", onResize);
  }, []);

  return size;
}

// ---------------------------------------------------------
// FREE-FLOATING CLUSTER POSITIONING
// ---------------------------------------------------------

function computeFloatingPosition(index, total, time, centerX, centerY) {
  const angle = (index / total) * Math.PI * 2;
  const baseRadius = 220;

  const driftX = Math.sin(time * 0.00008 + index * 1.7) * 18;
  const driftY = Math.cos(time * 0.00006 + index * 1.3) * 18;

  const wobbleX = Math.sin(time * 0.00012 + index * 2.1) * 8;
  const wobbleY = Math.cos(time * 0.0001 + index * 1.4) * 8;

  const cx = centerX + Math.cos(angle) * baseRadius + driftX + wobbleX;
  const cy = centerY + Math.sin(angle) * baseRadius * TILT + driftY + wobbleY;

  return { cx, cy };
}

// ---------------------------------------------------------
// GRID ARC LAYER
// ---------------------------------------------------------

function GridArcLayer({ centerX, centerY, radius }) {
  const width = window.innerWidth;
  const height = window.innerHeight;
  const arcRadius = radius * 1.1;

  const stroke = "rgba(248, 250, 252, 0.18)";
  const glow = "rgba(249, 115, 22, 0.55)";

  return (
    <svg
      width={width}
      height={height}
      style={{
        position: "relative",
        inset: 1,
        pointerEvents: "none",
        overflow: "visible",
      }}
    >
      <defs>
        <radialGradient id="arcGlow" cx="50%" cy="50%" r="50%">
          <stop offset="0%" stopColor={glow} stopOpacity="0.9" />
          <stop offset="100%" stopColor={glow} stopOpacity="0" />
        </radialGradient>
      </defs>

      <circle
        cx={centerX}
        cy={centerY}
        r={arcRadius * 1.3}
        fill="url(#arcGlow)"
        opacity={0.25}
        style={{ filter: "blur(40px)", transition: "opacity 0.6s ease" }}
      />

      <path
        d={describeArc(centerX, centerY, arcRadius, 200, 340)}
        fill="none"
        stroke={stroke}
        strokeWidth={2}
        strokeLinecap="round"
      />

      <path
        d={describeArc(centerX, centerY, arcRadius * 0.85, 100, 335)}
        fill="none"
        stroke="rgba(148, 163, 184, 0.35)"
        strokeWidth={2}
        strokeLinecap="round"
        strokeDasharray="4 6"
      />

      <path
        d={describeArc(centerX, centerY, arcRadius * 1.15, 300, 350)}
        fill="none"
        stroke="rgba(15, 23, 42, 0.9)"
        strokeWidth={3}
        strokeLinecap="round"
      />
    </svg>
  );
}

function polarToCartesian(cx, cy, r, angleInDegrees) {
  const angleInRadians = ((angleInDegrees - 90) * Math.PI) / 180.0;
  return {
    x: cx + r * Math.cos(angleInRadians),
    y: cy + r * Math.sin(angleInRadians),
  };
}

function describeArc(cx, cy, r, startAngle, endAngle) {
  const start = polarToCartesian(cx, cy, r, endAngle);
  const end = polarToCartesian(cx, cy, r, startAngle);
  const largeArcFlag = endAngle - startAngle <= 180 ? "0" : "1";

  return ["M", start.x, start.y, "A", r, r, 0, largeArcFlag, 0, end.x, end.y].join(" ");
}

// ---------------------------------------------------------
// CENTRAL HUB (BOTTOM CENTER)
// ---------------------------------------------------------

function CentralHub({ centerX, bottomY, onToggleAppMenu }) {
  const size = 96 * HUB_SCALE;

  return (
    <div
      onClick={onToggleAppMenu}
      style={{
        position: "absolute",
        left: centerX - size / 2,
        top: bottomY - size,
        width: size,
        height: size,
        borderRadius: "999px",
        background: "url('/logo.png') center/cover no-repeat",
        boxShadow: "0 0 50px rgba(249, 115, 22, 0.9), 0 0 150px rgba(15, 23, 42, 1)",
        border: "1px solid rgba(248, 250, 252, 0.25)",
        cursor: "pointer",
      }}
    />
  );
}

// ---------------------------------------------------------
// ARC BUTTONS
// ---------------------------------------------------------

function ArcButton({ cx, cy, radius, angle, label, onClick }) {
  const pos = polarToCartesian(cx, cy, radius, angle);
  const size = 40;

  return (
    <div
      onClick={onClick}
      style={{
        position: "absolute",
        left: pos.x - size / 2,
        top: pos.y - size / 2,
        width: size,
        height: size,
        borderRadius: "999px",
        background: "rgba(15, 23, 42, 0.9)",
        border: "1px solid rgba(148, 163, 184, 0.6)",
        boxShadow: "0 0 16px rgba(15, 23, 42, 0.9)",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        color: "#e2e8f0",
        cursor: "pointer",
        backdropFilter: "blur(10px)",
      }}
    >
      {label}
    </div>
  );
}

// ---------------------------------------------------------
// VIGNETTE
// ---------------------------------------------------------

function VignetteLayer({ active, x, y }) {
  const size = active ? 900 : 0;
  const opacity = active ? 0.65 : 0;

  return (
    <div
      style={{
        position: "absolute",
        inset: 0,
        pointerEvents: "none",
        opacity,
        transition: "opacity 0.4s ease",
        maskImage: active
          ? `radial-gradient(circle ${size}px at ${x}px ${y}px, transparent 0%, black 100%)`
          : "none",
        WebkitMaskImage: active
          ? `radial-gradient(circle ${size}px at ${x}px ${y}px, transparent 0%, black 100%)`
          : "none",
        backdropFilter: active ? "brightness(0.4)" : "none",
      }}
    />
  );
}

// ---------------------------------------------------------
// MAIN COMPONENT
// ---------------------------------------------------------

export default function OrbitalEngine({
  constellations,
  onLaunchApp,
  onToggleAppMenu,
  onOpenSettings,
  onOpenCalendar,
  onLogout,
}) {
  const { openContextMenu } = useContextMenu();

  const [time, setTime] = useState(0);
  const [mouseOffset, setMouseOffset] = useState({ x: 0, y: 0 });

  const [expandedClusterId, setExpandedClusterId] = useState(null);
  const [expandedClusterPos, setExpandedClusterPos] = useState(null);

  // Time smoothing
  useEffect(() => {
    let frame;
    const loop = () => {
      const t = performance.now() * TIME_SLOW;
      setTime((prev) => prev + (t - prev) * 0.05);
      frame = requestAnimationFrame(loop);
    };
    loop();
    return () => cancelAnimationFrame(frame);
  }, []);

  // Mouse parallax
  useEffect(() => {
    const handler = (e) => {
      const x = (e.clientX / window.innerWidth - 0.5) * 24;
      const y = (e.clientY / window.innerHeight - 0.5) * 24;
      setMouseOffset({ x, y });
    };
    window.addEventListener("mousemove", handler);
    return () => window.removeEventListener("mousemove", handler);
  }, []);

  // Click-away to close cluster
  useEffect(() => {
    if (!expandedClusterId) return;

    const handleClickAway = (e) => {
      if (e.target.closest("[data-cluster-expanded='true']")) return;
      setExpandedClusterId(null);
      setExpandedClusterPos(null);
    };

    window.addEventListener("mousedown", handleClickAway);
    return () => window.removeEventListener("mousedown", handleClickAway);
  }, [expandedClusterId]);

  // Right-click on empty space
  const handleRightClick = (e) => {
    e.preventDefault();
    openContextMenu(e, { type: "empty", onLaunchApp });
  };

  const { width, height } = useViewportSize();
  const centerX = width / 2;
  const centerY = height / 2;
  const bottomHubY = height - 80;

  // Build groups (free-floating)
  const groups = useMemo(() => {
    if (!Array.isArray(constellations)) return [];

    return constellations.map((c, index) => {
      const pos = computeFloatingPosition(
        index,
        constellations.length,
        time,
        centerX,
        centerY
      );
      return { constellation: c, index, ...pos };
    });
  }, [constellations, time, centerX, centerY]);

  const arcRadius = 520;

  return (
    <div style={rootStyle} onContextMenu={handleRightClick}>
      {/* Parallax background */}
      <div
        style={{
          ...hexLayerStyle,
          transform: `translate(${mouseOffset.x * 0.3}px, ${mouseOffset.y * 0.3}px)`,
        }}
      />
      <div
        style={{
          ...starfieldLayerStyle,
          transform: `translate(${mouseOffset.x * 0.6}px, ${mouseOffset.y * 0.6}px)`,
        }}
      />

      {/* Grid Arc */}
      <GridArcLayer centerX={centerX} centerY={centerY} radius={arcRadius} />

      {/* Scene */}
      <div
        style={{
          position: "absolute",
          inset: 0,
          transition: "transform 0.25s ease-out",
        }}
      >
        {/* Bottom Hub */}
        <CentralHub
          centerX={centerX}
          bottomY={bottomHubY}
          onToggleAppMenu={onToggleAppMenu}
        />

        {/* Clusters */}
        {groups.map((g) => (
          <ClusterNode
            key={g.constellation.id}
            group={g}
            time={time}
            expanded={expandedClusterId === g.constellation.id}
            onExpand={() => {
              setExpandedClusterId(g.constellation.id);
              setExpandedClusterPos({ x: g.cx, y: g.cy });
            }}
            onLaunchApp={(appId) => {
              onLaunchApp(appId);
              setExpandedClusterId(null);
              setExpandedClusterPos(null);
            }}
          />
        ))}

        {/* Arc buttons */}
        <ArcButton
          cx={centerX}
          cy={centerY}
          radius={arcRadius * 1.1}
          angle={250}
          label={<Settings size={25} color="white" />}
          onClick={onOpenSettings}
        />

        <ArcButton
          cx={centerX}
          cy={centerY}
          radius={arcRadius * 1.1}
          angle={300}
          label={<Calendar size={30} color="white" />}
          onClick={onOpenCalendar}
        />

        <ArcButton
          cx={centerX}
          cy={centerY}
          radius={arcRadius * 1.1}
          angle={240}
          label={<LogOut size={25} color="white" />}
          onClick={onLogout}
        />
      </div>

      {/* Clock Widget */}
      <DesktopClockWidget />

      {/* Vignette */}
      <VignetteLayer
        active={!!expandedClusterId}
        x={expandedClusterPos?.x}
        y={expandedClusterPos?.y}
      />
    </div>
  );
}

// ---------------------------------------------------------
// STYLES
// ---------------------------------------------------------

const rootStyle = {
  position: "relative",
  width: "100%",
  height: "100%",
  overflow: "hidden",
  backgroundColor: "#020617",
};

const hexLayerStyle = {
  position: "absolute",
  inset: 0,
  backgroundImage:
    "radial-gradient(circle at 20% 30%, rgba(249, 115, 22, 0.12) 0, transparent 55%), radial-gradient(circle at 80% 70%, rgba(248, 250, 252, 0.08) 0, transparent 60%)",
  mixBlendMode: "screen",
  opacity: 0.6,
  pointerEvents: "none",
};

const starfieldLayerStyle = {
  position: "absolute",
  inset: 0,
  backgroundImage:
    "radial-gradient(circle at 10% 20%, rgba(148, 163, 184, 0.35) 0, transparent 55%), radial-gradient(circle at 90% 80%, rgba(59, 130, 246, 0.25) 0, transparent 60%)",
  opacity: 0.45,
  mixBlendMode: "screen",
  pointerEvents: "none",
};