import React, { useState, useEffect, useCallback, useMemo } from "react";
import { MessageSquare } from "lucide-react";

import { useAuth } from "./hooks/useAuth";
import { useFileSystem } from "./hooks/useFileSystem";
import { useWindows } from "./hooks/useWindows";
import { useSettings } from "./hooks/useSettings";
import { useIsMobile } from "./hooks/useIsMobile";
import { useConstellations } from "./hooks/useConstellations";

import { getTheme } from "./utils/themes";

import { TipsWidget } from "./components/system/TipsWidget";
import LoadingScreen from "./components/shared/LoadingScreen";
import LoginPanel from "./components/auth/LoginPanel";

import OrbitalEngine from "./components/desktop/OrbitalEngine";

import Window from "./components/desktop/Window";
import AppMenu from "./components/desktop/AppMenu";
import SettingsPanel from "./components/desktop/SettingsPanel";

import Breeze from "./components/apps/Breeze/Breeze";

import { SearchPalette } from "./components/system/SearchPalette";
import { SearchPaletteController } from "./controllers/SearchPaletteController";

import {
  ContextMenuProvider,
  useContextMenu,
} from "./context/ContextMenuContext";
import ContextMenu from "./components/shared/ContextMenu";

const AirOSInner = () => {
  const [loginMode, setLoginMode] = useState("login");
  const [currentPath] = useState("/");
  const [isAppMenuOpen, setIsAppMenuOpen] = useState(false);
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);

  const isMobile = useIsMobile();
  const { openContextMenu } = useContextMenu();

  // Constellations
  const { constellations } = useConstellations();

  // Search Palette
  const [paletteController] = useState(
    () =>
      new SearchPaletteController({
        browserController: null,
        fileSystem: null,
      })
  );
  const [paletteVisible, setPaletteVisible] = useState(false);

  // Auth
  const { currentUser, isLoading, login, register, logout } = useAuth();

  // File system
  const {
    fileSystem,
    saveFile,
    createDirectory,
    deleteFile,
    reloadFileSystem,
  } = useFileSystem(currentUser?.id);

  // Inject FS into palette controller
  useEffect(() => {
    if (fileSystem && paletteController.fileSearch) {
      paletteController.fileSearch.fs = fileSystem;
    }
  }, [fileSystem, paletteController]);

  // Settings
  const settings = useSettings(currentUser?.id);

  // Window manager
  const {
    windows,
    activeWindow,
    setActiveWindow,
    createWindow,
    closeWindow,
    minimizeWindow,
    restoreWindow,
    maximizeWindow,
    updateWindowPosition,
  } = useWindows();

  // Fullscreen on login
  useEffect(() => {
    if (!currentUser) return;
    const el = document.documentElement;
    el.requestFullscreen?.();
    el.webkitRequestFullscreen?.();
    el.mozRequestFullScreen?.();
    el.msRequestFullscreen?.();
  }, [currentUser]);

  // Theme
  const currentTheme = useMemo(
    () => getTheme(settings.theme),
    [settings.theme]
  );

  // Palette visibility subscription
  useEffect(() => {
    const unsubscribe = paletteController.subscribe(() => {
      setPaletteVisible(paletteController.visible);
    });
    return unsubscribe;
  }, [paletteController]);

  // Ctrl+Space  palette
  useEffect(() => {
    const handler = (e) => {
      if (e.ctrlKey && e.code === "Space") {
        e.preventDefault();
        paletteController.show();
      }
    };
    window.addEventListener("keydown", handler);
    return () => window.removeEventListener("keydown", handler);
  }, [paletteController]);

  // Global right-click  OS context menu
  useEffect(() => {
    const handler = (e) => {
      if (
        e.target.closest(
          'input, textarea, [contenteditable="true"], [data-no-context]'
        )
      ) {
        return;
      }

      e.preventDefault();

      openContextMenu(e, {
        type: "global",
        actions: [
          {
            label: "Open URL / Search",
            action: () => paletteController.show(),
          },
        ],
      });
    };

    window.addEventListener("contextmenu", handler);
    return () => window.removeEventListener("contextmenu", handler);
  }, [openContextMenu, paletteController]);

  // -----------------------------
  // File Opening Logic
  // -----------------------------
  const handleOpenFile = useCallback(
    (name, path) => {
      const file = fileSystem[path];
      if (!file) return console.warn("File not found:", path);

      const lowerPath = path.toLowerCase();

      const buildMediaSrc = () => {
        if (
          typeof file.content === "string" &&
          file.content.startsWith("data:")
        ) {
          return file.content;
        }

        const ext = lowerPath.split(".").pop();
        const mimeMap = {
          mp4: "video/mp4",
          webm: "video/webm",
          ogg: "video/ogg",
          mov: "video/quicktime",
          mp3: "audio/mpeg",
          wav: "audio/wav",
          flac: "audio/flac",
          m4a: "audio/mp4",
          aac: "audio/aac",
        };
        const mime = mimeMap[ext] || "application/octet-stream";

        return `data:${mime};base64,${file.content || ""}`;
      };

      if (/\.(jpg|jpeg|png|gif|svg|webp)$/i.test(lowerPath)) {
        createWindow("image", {
          title: name,
          src: buildMediaSrc(),
          filePath: path,
        });
        return;
      }

      if (/\.(mp4|webm|ogg|mov|mp3|wav|flac|m4a|aac)$/i.test(lowerPath)) {
        createWindow("video", {
          title: name,
          src: buildMediaSrc(),
          width: 1280,
          height: 940,
        });
        return;
      }

      createWindow("text", {
        title: name,
        filePath: path,
        width: 1800,
        height: 1280,
      });
    },
    [fileSystem, createWindow]
  );

  // -----------------------------
  // App Launching Logic
  // -----------------------------
  const handleLaunchApp = useCallback(
    (appType, extraProps = {}) => {
      if (appType === "Breeze") {
        createWindow("Breeze", {
          title: "Breeze: Messaging and Circles",
          width: 900,
          height: 650,
          icon: MessageSquare,
          component: Breeze,
          props: { currentUser, ...extraProps },
        });
        return;
      }

      if (appType === "files") {
        createWindow("files", { title: "File Manager", ...extraProps });
        return;
      }

      if (appType === "Browser") {
        createWindow("Browser", {
          title: "Browser",
          width: 1200,
          height: 800,
          ...extraProps,
        });
        return;
      }

      createWindow(appType, { title: appType, ...extraProps });
    },
    [createWindow, currentUser]
  );

  // -----------------------------
  // UI Toggles
  // -----------------------------
  const handleToggleAppMenu = useCallback(
    () => setIsAppMenuOpen((prev) => !prev),
    []
  );

  const handleOpenSettings = useCallback(
    () => setIsSettingsOpen(true),
    []
  );

  const handleOpenCalendar = useCallback(() => {
    const calendarWindow = windows.find((w) => w.app === "calendar");

    if (calendarWindow) {
      setActiveWindow(calendarWindow.id);
      if (calendarWindow.minimized) restoreWindow(calendarWindow.id);
    } else {
      createWindow("calendar", {
        title: "Calendar",
        width: 900,
        height: 650,
      });
    }
  }, [windows, setActiveWindow, restoreWindow, createWindow]);

  // -----------------------------
  // Loading / Login
  // -----------------------------
  if (isLoading) return <LoadingScreen onSkip={() => {}} />;

  if (!currentUser) {
    return (
      <LoginPanel
        onLogin={login}
        onRegister={register}
        loginMode={loginMode}
        setLoginMode={setLoginMode}
      />
    );
  }

  // -----------------------------
  // Main OS UI
  // -----------------------------
  return (
    <div
      style={{
        width: "100%",
        height: "98.7vh",
        display: "flex",
        flexDirection: "column",
        background: currentTheme.gradient,
        overflow: "hidden",
        transition: "background 0.5s ease",
      }}
    >
      {/* Desktop (Orbital only) */}
      <div style={{ flex: 2, position: "relative" }}>
        <OrbitalEngine
          constellations={constellations}
          onLaunchApp={handleLaunchApp}
          onToggleAppMenu={handleToggleAppMenu}
          onOpenSettings={handleOpenSettings}
          onOpenCalendar={handleOpenCalendar}
          onLogout={logout}
        />

        {/* Universal Search Palette */}
        <SearchPalette
          visible={paletteVisible}
          result={paletteController.lastResult}
          onClose={() => paletteController.hide()}
          onSubmit={(value) =>
            paletteController.submit(value, handleLaunchApp)
          }
          onOpenFile={(file) => handleOpenFile(file.name, file.path)}
        />

        {/* Windows */}
        {windows.map((win) => (
          <Window
            key={win.id}
            window={win}
            activeWindow={activeWindow}
            setActiveWindow={setActiveWindow}
            onMinimize={minimizeWindow}
            onMaximize={maximizeWindow}
            onClose={closeWindow}
            onUpdatePosition={updateWindowPosition}
            fileSystem={fileSystem}
            saveFile={saveFile}
            createDirectory={createDirectory}
            deleteFile={deleteFile}
            reloadFileSystem={reloadFileSystem}
            userId={currentUser?.id}
            currentPath={currentPath}
            onOpenFile={handleOpenFile}
            createWindow={createWindow}
            currentUser={currentUser}
            updateTheme={settings.updateTheme}
            mobile={isMobile}
          />
        ))}

        {/* App Menu (opened via central hub in OrbitalEngine) */}
        <AppMenu
          isOpen={isAppMenuOpen}
          onClose={() => setIsAppMenuOpen(false)}
          onLaunchApp={handleLaunchApp}
          onOpenSettings={handleOpenSettings}
        />

        {/* Settings Panel */}
        <SettingsPanel
          isOpen={isSettingsOpen}
          onClose={() => setIsSettingsOpen(false)}
          settings={settings}
          onUpdateSetting={(key, value) => {
            const updateFn =
              settings[
                `update${key.charAt(0).toUpperCase() + key.slice(1)}`
              ];
            if (updateFn) updateFn(value);
          }}
        />

        {/* Global Context Menu */}
        <ContextMenu />
      </div>

      {settings.showTipsWidget && (
        <TipsWidget
          onDisable={() => settings.updateShowTipsWidget(false)}
        />
      )}
    </div>
  );
};

const AirOS = () => (
  <ContextMenuProvider>
    <AirOSInner />
  </ContextMenuProvider>
);

export default AirOS;