import React, { useEffect, useRef } from 'react';

export const BrowserContent = ({ url }) => {
  const isElectron = navigator.userAgent.includes("Electron");

  if (isElectron) {
    return (
      <webview
        src={url}
        style={{
          width: "100%",
          height: "100%",
          border: "none",
          flex: 1
        }}
        allowpopups="true"
        webpreferences="nativeWindowOpen=yes"
      />
    );
  }

  // Web mode fallback
  return (
    <iframe
      src={url}
      title="browser-content"
      style={{
        flex: 1,
        border: 'none',
        width: '100%',
        height: '100%',
      }}
    />
  );
};